































































































































































import { Vue, Component } from "vue-property-decorator";

@Component
export default class OrderConfirm extends Vue {
  public cartList = [] as any;

  public created() {
    this.getCartList();
    this.$store.commit("changeBreadText", "Order Confirm");
  }
  setCartList(list: any) {
    return this.$store.commit("setCartList", list);
  }
  public async getCartList() {
    const response = (await this.axios.get("/users/checkedCartList")).data;
    if (response.status === "0") {
      this.cartList = response.result;
    } else {
      alert(response.msg);
    }
  }

  public async orderSuccess() {
    const response = (await this.axios.post("/users/orderSuccess")).data;
    if (response.status === "0") {
      this.setCartList(response.result.cartList);
      this.$router.push("/orderSuccess");
    } else {
      alert(response.msg);
    }
  }

  public get orderTotal(): number {
    return this.cartList.reduce(
      (acc: any, cur: any) => acc + cur.productNum * cur.salePrice,
      0
    );
  }
}
