















































































































































































































































import { Vue, Component } from "vue-property-decorator";
import Modal from "@/components/Modal.vue";
interface AddressInfo {
  [name: string]: any;
}
@Component({
  components: {
    Modal
  }
})
export default class Address extends Vue {
  public addressList = [] as any;
  public addAddressModal: boolean = false;
  public limit: number = 3;
  public checkIndex: number = 0;
  public delAddressModel: boolean = false;
  public delAddressId: string = "";
  public addressInfo: AddressInfo = {
    addressId: "",
    userName: "",
    streetName: "",
    postCode: "",
    tel: "",
    isDefault: false
  };
  public created(): void {
    this.$store.commit("changeBreadText", "My Address");
    this.getAddressList();
  }
  public async getAddressList() {
    const response = (await this.axios.get("/users/addressList")).data;
    if (response.status === "0") {
      this.addressList = response.result;
    } else {
      alert(response.msg);
    }
  }
  public async addAddress() {
    for (const item in this.addressInfo) {
      if (
        item !== "addressId" &&
        item !== "isDefault" &&
        !Boolean(this.addressInfo[item])
      ) {
        return;
      }
    }
    this.addressInfo.addressId = `${Date.now()}`;
    const response = (await this.axios.post("/users/addAddress", {
      addressInfo: this.addressInfo
    })).data;
    if (response.status === "0") {
      this.addressList.push(this.addressInfo);
      this.limit = this.addressList.length;
      this.addAddressModal = false;
    } else {
      alert(response.msg);
    }
  }
  public closeModal() {
    this.addAddressModal = false;
    this.delAddressModel = false;
  }
  public async setDefault(addressId: string) {
    const response = (await this.axios.post("/users/setDefault", {
      addressId
    })).data;
    if (response.status === "0") {
      this.addressList.forEach((item: any) => {
        item.isDefault = item.addressId === addressId;
      });
    } else {
      alert(response.msg);
    }
  }
  public async delAddress() {
    const response = (await this.axios.post("/users/delAddress", {
      addressId: this.delAddressId
    })).data;
    if (response.status === "0") {
      const delAddressIndex = this.addressList.findIndex(
        (item: any) => item.addressId === this.delAddressId
      );
      this.addressList.splice(delAddressIndex, 1);
      this.delAddressModel = false;
    } else {
      alert(response.msg);
    }
  }
  public get addressListFilter() {
    return this.addressList.slice(0, this.limit);
  }
}
